
@media (min-width:600px) {
  #drum-pads {
    grid-template-columns: repeat(3,10px);
  }
}
#drum-machine {
  background-color: grey;
}
#display {
  height: 60vh;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 60px;
  margin-bottom: 100px;
}

#drum-pads {
  height: 60vh;
  width: 100vw;
  display: grid;
   grid-template-columns: repeat(3,500px);
   grid-template-rows: 100px 100px; 
  grid-row: 1;
  justify-content: center;
  align-items: center;
  grid-gap: 10px;
}

.drum-pad {
  border: 1px solid black;
  height: 100px;
  /* width: 15vh;  */
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 600px;
  background-color: #84eeff;
}
